import cssProps from './css-props'

/**
 * Returns the joined style as the string, or a hash with the style rules.
 * @param {string} style
 * @param {Object<string, ?>} props The properties of the component.
 */
const controlStyle = (style, props) => {
  const fst = Object.keys(props).reduce((acc, key) => {
    if (!cssProps.includes(key)) return acc
    const val = props[key]
    acc[key] = val
    delete props[key]
    return acc
  }, {})
  if (!style) return fst

  let ar = Object.keys(fst).map((k) => `${k}:${fst[k]}`)
  if (style) ar.push(style)
  const j = ar.join(';')
  return j
}

export default controlStyle